html {
    box-sizing: border-box;
  }
  
  body {
    // margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
      'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
      'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #faf8f8;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  .input {
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 40vw;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  
    &::placeholder {
      font-size: 1.5vw;
      text-align: center;
      color: gray;
    }
  
    &:focus {
      color: #495057;
      background-color: #fff;
      border-color: #80bdff;
      outline: 0;
      box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
    }
  }
  
  .countries {
    margin-top: 10px;
    padding: 10px;
    width: 60vw;
    margin-left: auto;
    margin-right: auto;
    border: gray 1px solid;
  }
  
  .country-name-list {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }
  
  .country-name {
    text-align: center;
    margin-top: 0;
    font-size: 3vw;
  }
  .country-wrapper {
    display: flex;
    justify-content: center;
  }
  .country-capital {
    font-size: 1.2vw;
  }
  
  .country-popolation {
    font-size: 1.2vw;
  }
  
  .country-languages {
    font-size: 1.2vw;
  }
  .bold {
    margin-right: 10px;
    font-size: 1.2vw;
    font-weight: 700;
  }
  .country-flag {
    display: block;
    width: 50%;
    margin-left: 10px;
  }